                                    <h1>1000.00</h1>
                                </div>
                                <div>
                                    <p>昨日利润</p>
                                    <h1>1000.00</h1>
                                </div>
                            </li>
                            <li>
                                <div>
                                    <p>直属团长</p>
                                    <h1>10个</h1>
                                </div>
                                <div>
                                    <p>直属子团长</p>
                                    <h1>10个</h1>
                                </div>
                            </li>
                            <!-- <li>
                                <div>
                                    <p>直属组长</p>
                                    <h1>10个</h1>
                                </div>
                                <div>
                                    <p>直属子组长</p>
                                    <h1>10个</h1>
                                </div>
                            </li> -->
                        </ul>
                    </div>
                </div>
            </el-col>
            <el-col :span="12">
                <div class="module">
                    <div class="module-title">
                        <span class="vertical-lines"></span>
                        <span class="title-text">今日数据统计</span>
                    </div>
                    <div class="module-content">
                        <ul class="module-content-list">
                            <li>
                                <div class="data1">
                                    <p>今日订单数量</p>
                                    <h1>2</h1>
                                </div>
                                <div class="data2">
                                    <p>与昨日数量比</p>
                                    <h1>5%</h1>
                                </div>
                            </li>
                            <li>
                                <div class="data1">
                                    <p>今日订单总金额</p>
                                    <h1>2</h1>
                                </div>
                                <div class="data2">
                                    <p>与昨日金额比</p>
                                    <h1>5%</h1>
                                </div>
                            </li>
                            <li>
                                <div class="data1">
                                    <p>今日下单商户数</p>
                                    <h1>2</h1>
                                </div>
                                <div class="data2">
                                    <p>与昨日数量相比</p>
                                    <h1>5%</h1>
                                </div>
                            </li>
                            <li>
                                <div class="data1">
                                    <p>平台拥有总商户数</p>
                                    <h1>2564</h1>
                                </div>
                                <div class="data2">
                                    <p>昨日总商户数</p>
                                    <h1>2500</h1>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </el-col>
            <el-col :span="6">
                <div class="module">
                    <div class="module-title">
                        <span class="vertical-lines"></span>
                        <span class="title-text">出单时段统计</span>
                    </div>
                    <div class="module-content">
                        <ul class="module-content-list">
                            <li>
                                <div class="data1">
                                    <p>今日订单数量</p>
                                    <h1>2</h1>
                                </div>
                                <div class="data2">
                                    <p>与昨日数量比</p>
                                    <h1>5%</h1>
                                </div>
                            </li>
                            <li>
                                <div class="data1">
                                    <p>今日订单总金额</p>
                                    <h1>2</h1>
                                </div>
                                <div class="data2">
                                    <p>与昨日金额比</p>
                                    <h1>5%</h1>
                                </div>
                            </li>
                            <li>
                                <div class="data1">
                                    <p>今日下单商户数</p>
                                    <h1>2</h1>
                                </div>
                                <div class="data2">
                                    <p>与昨日数量相比</p>
                                    <h1>5%</h1>
                                </div>
                            </li>
                            <li>
                                <div class="data1">
                                    <p>平台拥有总商户数</p>
                                    <h1>2564</h1>
                                </div>
                                <div class="data2">
                                    <p>昨日总商户数</p>
                                    <h1>2500</h1>
                                </div>
                            </li>
                        </ul>
                    </div>
                </div>
            </el-col>
        </el-row>
    </div>
</template>

<script>
export default {
    data() {
        return {
            pandectData: {},
            dataList: {} // 个人数据
        };
    },
    created() {
        this.getPandect();
        this.requestInfo();
    },
    mounted() {
        this.bindEvent();
    },
    methods: {
        /**
         * 绑定事件
         */
        bindEvent() {
            $(".pavilion-inner .tabs li").hover(function() {
                var i = $(this).index();
                $(this)
                    .addClass("on")
                    .siblings()
                    .removeClass("on");
                $(".pavilion-inner .tab-content")
                    .removeClass("show")
                    .eq(i)
                    .addClass("show");
            });
        },

        /**
         * 获取个人信息
         */
        requestInfo() {
            this.$axios.post("Admin/user_info").then(data => {
                this.dataList = data.dataList;
            });
        },

        /**
         * 获取总览数据
         */
        getPandect() {
            this.$axios.post("admin/admin_info").then(data => {
                if (data.status != "OK") return;
                this.pandectData = data.dataList;
            });
        }
    }
};
</script>


