export default class Data {
	constructor(Vue) {
		this.Vue = Vue;

		// 转换
		this.tg = {
			has_pay: {
				0: '未支付',
				1: '支付成功',
			},
			has_pay_color: {
				0: 'red',
				1: 'green'
			},
			has_upload: {
				0: '未上传',
				1: '已上传',
			},
			has_upload_color: {
				0: 'red',
				1: 'green'
			},
			pay_status: {
				0: '正常',
				1: '超时',
				2: '废弃'
			},
			pay_status_color: {
				0: '',
				1: 'red',
				2: 'gray'
			},
			account_status: {
				0: '正常接单',
				1: '系统封号',
				2: '系统暂停',
				3: '手动暂停',
				4: '风控暂停',
			},
			account_status_color: {
				0: 'green',
				1: 'red',
				2: 'gray',
				3: 'gray',
				4: 'gray',
			},
		}


		// 统一页容量
		this.pageSizes = [100, 200, 500, 1000];


		// 正则
		this.reg = {
			ip: /^(0|\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(0|\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(0|\d{1,2}|1\d\d|2[0-4]\d|25[0-5])\.(0|\d{1,2}|1\d\d|2[0-4]\d|25[0-5])$/,
		}
	}
}



